.social_card{
  width: 500px;
  background-color: #8BC6EC;
  background-image: linear-gradient(35deg, #8BC6EC 0%, #9599E2 100%);
  box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
  border-radius: 20px;
  height: 250px;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  transition: transform 0.5s;
  cursor: pointer;
  margin-top: 20px;
}

.social_card:hover{
  transform: scale(1.1) rotate(5deg);
}
.social_card_left{
  display: flex;
  flex-direction: column;
  width: 55%;
  height: 100%;
  align-items: center;
  justify-content: center;
}
.social_card_left h1{
  margin-top: -20px;
  text-align: center;
  font-size: 25px;
}
.social_card_left span{
  font-size: 30px;
}
.social_card_right{
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 30px;
}
.social_card_right i{
  text-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  font-size: 100px;
  margin-right: 10px;
}

.alert_copied{
  z-index: 2;
  margin-bottom: 5px;
}

.hidden{
  opacity: 0;
}
.social_card_content{
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin: 50px;
  transition: transform 0.5s;

}
.text_small{
  width: 100%;
  text-align: center;
}

@media(max-width: 768px) {
  .social_card_content{
    transform: scale(0.6);
    margin: 10px;
    margin-top: -50px;
  }
  .social_card:hover{
    transform: scale(0.6) rotate(5deg);
    
  }
} 